
/* Tickets CTA */

// Footer
.footer {
  padding: 25px 25px 10px 25px;
  background-color: $darker-green;
}

.footer p {
  margin: 0;
  color: $beige;
  text-align: center;
  font-size: 14px;
}

.footer-brand {
  height: 50px;
  width: 100%;
  margin: 0 auto 10px;
  background: url("data:image/png;base64,")
    no-repeat center center;
}

.footer .social-medias {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 2px solid transparentize($beige, 0.5);
  width: 50%;
  margin: 0 auto;
  margin-top: 20px;
  padding-top: 20px;

  a {
    display: inline-block;
    margin: 0 40px 10px;
  }

  @include tablet {
    width: 90%;
  }
}

.tickets-cta {
  background-color: $lighter-red;
  color: $darker-red;
  position: relative;

  .section-title {
    margin: $base-horizontal-padding-mobile auto;
  }

  .content {
    margin: auto;
    max-width: 700px;

    a {
      margin-top: 15px;
    }
  }

  .detail {
    position: absolute;
    top: 0;
    right: 0;
    transform: translateY(-50%);
    height: 65px;
  }

  @include tablet {
    max-width: 100%;
  }

  @include mobile {
    .content .ctas {
      display: flex;
      flex-flow: column nowrap;
    }
  }
}
